.pageNumConteiner {
    max-width: 400px;
    overflow: hidden;
}

.pageItem {
    margin: 0 5px 0 0;
    cursor: pointer;
}

.currentPage {
    font-weight: bold;
}

.inner {
    display: block;
    text-decoration: none;
}

.item {
    margin:0 -2px 0 0;
    padding: 2px 5px;
    display: inline-block;
    text-decoration: none;
    border: 2px rgb(255, 91, 27) solid;
}

.itemActive {
    font-weight: bold;
    
    background-color: rgb(255, 91, 27);
}

.link {
    text-decoration: none;
    color: rgb(255, 91, 27);
}

.linkActive {
    color:white;
}



